<template>
  <div class="customer-list">
    <!-- S 头部 -->
    <Header></Header>
    <!-- E 头部 -->
    
    <!-- S 客户列表 -->
    <div class="customer-content">
      <div class="scroll-list-wrap">
        <cube-scroll
          ref="scroll"
          :data="customers"
          :options="options"
          @pulling-up="onPullingUp">
          <ul class="list-wrapper">
            <router-link :to="'/customer/detail/'+customer.id" tag="div" v-for="(customer, index) in customers">
            <li class="list-item">
              <img class="thumb" :src="customer.pic">
              <div class="text">
                <h5 v-text="customer.name"></h5>
                <span v-text="customer.phone"></span>
                <p v-text="customer.address"></p>
              </div>
            </li>
            </router-link>
          </ul>
        </cube-scroll>
      </div>
    </div>
    <!-- E 客户列表 -->
  </div>
</template>

<script>
import Header from '@/components/Header';
import pic01 from '../../assets/pics/g01.jpg'
import pic02 from '../../assets/pics/g02.jpg'
import pic03 from '../../assets/pics/g03.jpg'
export default {
  data () {
    return {
      pullUpLoad: true,
      pullUpLoadThreshold: 0,
      pullUpLoadMoreTxt: '加载更多',
      pullUpLoadNoMoreTxt: '已无更多',
    	customers: [{
            id: 1,
            name: '阿三',
            address: '湖南省长沙市芙蓉区芙蓉广场 A座1006',
            phone: '13054161001',
            pic: pic01
          }, {
            id: 2,
            name: '阿四',
            address: '湖南省长沙市芙蓉区芙蓉广场 B座1005',
            phone: '13054161001',
            pic: pic02
          }, {
            id: 3,
            name: '阿五',
            address: '湖南省长沙市开福区五一广场 A座1006',
            phone: '13054161001',
            pic: pic03
          }, {
            id: 4,
            name: '阿六',
            address: '湖南省长沙市天心区华远广场 A座1006',
            phone: '13054161001',
            pic: pic01
          }, {
            id: 5,
            name: '阿七',
            address: '湖南省长沙市开福区五一广场 C座1006',
            phone: '13054161001',
            pic: pic01
          }, {
            id: 6,
            name: '阿八',
            address: '湖南省长沙市开福区五一广场 C座2101',
            phone: '13054161001',
            pic: pic02
          }, {
            id: 7,
            name: '阿九',
            address: '湖南省长沙市芙蓉区芙蓉广场 C座1005',
            phone: '13054161001',
            pic: pic03
          }, {
            id: 8,
            name: '阿十',
            address: '湖南省长沙市芙蓉区芙蓉广场 D座1005',
            phone: '13054161001',
            pic: pic01
        }]
    }
  },
  props: {
  },
  computed: {
    options() {
      return {
        pullUpLoad: this.pullUpLoadObj,
        scrollbar: false
      }
    },
    pullUpLoadObj: function() {
      return this.pullUpLoad ? {
        threshold: parseInt(this.pullUpLoadThreshold),
        txt: {
          more: this.pullUpLoadMoreTxt,
          noMore: this.pullUpLoadNoMoreTxt
        }
      } : false
    }
  },
  methods: {
    onPullingUp() {
      console.log('加载更多...');
      // 模拟更新数据
      setTimeout(() => {
        if (Math.random() > 0.5) {
          // 如果有新数据
          let newPage = this.customers.slice(0, 5)
          this.customers = this.customers.concat(newPage)
        } else {
          // 如果没有新数据
          this.$refs.scroll.forceUpdate()
        }
      }, 1000)
    }
  },
  components: {
    Header
  }
}
</script>
<style lang="scss">
@import '~SCSS/customer/list';
</style>
